















import Vue from 'vue'

export default Vue.extend({
  name: 'TRACKLIST',
  props: {
    item: {
      type: String
    },
    index: {
      type: Number
    }
  },
  data: () => ({
    type: true
  }),
  methods: {
    changeTypeBtn (): void {
      if (this.type && this.$refs !== undefined) {
        (this as any).$refs.audio.play()
      } else (this as any).$refs.audio.pause()
      this.$emit('getIndex', this.index)
      this.type = !this.type
    }
  }
})
